/* colors */
:root {
  --PrimaryColor: hsl(26, 93%, 50%);
  --HoverColor: hsl(26, 90%, 42%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --whiteColorDeam: hsl(0, 0%, 93%);
  --greybg: hsl(0, 0%, 96%);
  --greyText: rgb(190, 190, 190);
  --inputColor: hsl(330, 12%, 97%);
}

.navBarSection {
  .header {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: transparent;
    width: 100%;
    height: 10%;
    padding: 1rem;
    z-index: 1000;

    .logoDiv {
      .logo {
        color: var(--whiteColor);
        font-weight: 500;
        cursor: pointer;
        align-items: center;
        justify-content: center;

        .icon {
          width: 8%;
          font-size: 25px;
          margin-right: 10px;
        }
      }
    }

    @media screen and (max-width: 768px) {
      .navBar {
        background: var(--greybg);
        position: absolute;
        height: max-content;
        padding: 1rem;
        width: 100%;
        top: 0;
        left: -500%;
        box-shadow: 0 2px 4px rgba(209, 209, 209, 0.904);
        transition: 0.5s ease;
        z-index: 1000;

        .navLists {
          flex-direction: column;
          justify-content: center;
          width: 100%;
          margin: auto;

          .navItem {
            padding: 1rem 0;

            .navLink {
              color: var(--textColor);
              font-size: 0.9rem;
              font-weight: 600;
              &:hover {
                color: var(--PrimaryColor);
              }
            }
          }
          .headerBtns {
            flex-direction: column;
            .btn {
              margin-top: 1rem;

              a {
                font-weight: 600;
                &:hover {
                  color: var(--whiteColor);
                }
              }
            }
            .loginBtn {
              background: none;
              border: 1px solid var(--PrimaryColor);
              a {
                columns: var(--PrimaryColor);
              }
              &:hover {
                background: var(--HoverColor);
                a {
                  color: var(--whiteColor);
                }
              }
            }
          }
        }

        .closeNavbar {
          position: absolute;
          top: 1rem;
          right: 1.5rem;
          color: var(--PrimaryColor);
          &:hover .icon {
            color: var(--HoverColor);
          }
        }
      }
      .activeNavbar {
        left: 0;
      }
    }
  }

  .activeHeader {
    // background: var(--greyText);
    background: rgb(114 105 105);
    box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.45);
  }
}

//MEDIA AUERIS====================>
@media screen and (min-width: 769px) {
  .toggleNavbar,
  .closeNavbar {
    display: none;
  }

  .header {
    padding: 1.5rem 2rem;
    .navLists {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: row;

      .navItem {
        .navLink {
          color: var(--whiteColor);
          padding: 0 0.45rem;
          font-size: 0.9rem;
          font-weight: 400;
          &:hover {
            color: var(--PrimaryColor);
          }
        }
      }

      .btn {
        margin-left: 1rem;
        a {
          color: var(--whiteColor);
          font-weight: 500;
        }
      }

      .loginBtn {
        background: none;
        border: 1px solid var(--PrimaryColor);

        &:hover {
          background: var(--PrimaryColor);
        }
        a {
          color: var(--whiteColor);
        }
      }
    }
  }
}

@media screen and (min-width: 960px) {
  .header {
    .navBar {
      .navLists {
        .navItem {
          padding: 0 0.7rem;
        }
      }
    }
  }
}

@media screen and (min-width: 1024px) {
  .header {
    padding: 1rem 8.5rem;
  }
}
