/* colors */
:root {
  --PrimaryColor: hsl(26, 93%, 50%);
  --HoverColor: hsl(26, 90%, 42%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --whiteColorDeam: hsl(0, 0%, 93%);
  --greybg: hsl(0, 0%, 96%);
  --greyText: rgb(190, 190, 190);
  --inputColor: hsl(330, 12%, 97%);
}

.home {
  height: 90vh;
  background: linear-gradient(rgba(33, 33, 33, 0.522), rgba(33, 33, 33, 0.522)),
    url("../../Assets/bgImage.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  position: relative;
}
.secContainer {
  .homeText {
    text-align: center;
    .title {
      color: var(--whiteColor);
      font-size: 2rem;
      font-weight: 700;
      line-height: 2.5rem;
    }

    .subTitle {
      color: var(--whiteColor);
      opacity: 0.9;
      font-size: 13px;
      font-weight: 300;
      padding: 1rem 0;
      max-width: 70%;
      text-align: center;
      margin: auto;
    }

    .btn {
      padding: 0.6rem 1.5rem;
    }
  }

  .homeCard {
    width: 80%;
    border-radius: 1rem;
    padding: 2rem;
    background: var(--whiteColor);
    position: absolute;
    gap: 1rem;
    justify-content: center;

    bottom: -101px;
    transform: translate(-50%);
    left: 50%;
    box-shadow: 0 2px 8px 4px rgba(178, 178, 178, 0.45);

    .locationDiv,
    .distDiv,
    .priceDiv {
      width: 100%;

      label {
        display: block;
        color: var(--textColor);
        font-weight: 500;
        font-size: 13px;
        padding-bottom: 0.4rem;
      }

      input {
        width: 100%;
        background: var(--inputColor);
        border-radius: 10px;
        border: none;
        padding: 10px;
        font-size: 13px;
        font-weight: 500;

        &::placeholder {
          font-size: 13px;
          opacity: 0.5;
          font-weight: 400;
        }
        &:focus {
          outline: none;
        }
      }
    }

    .btn {
      width: 100%;
      padding: 0.6rem 1.5rem;
      color: var(--whiteColor);
      font-weight: 600;
    }
  }
}

//media queries===========>

@media screen and (min-width: 480px) {
  .home {
    .secContainer {
      .homeCard {
        bottom: -100px;
        width: 90%;

        grid-template-columns: repeat(2, 1fr);
        .btn {
          transform: translateY(15px);
        }
      }
    }
  }
}

@media screen and (min-width: 556px) {
  .home {
    .secContainer {
      .homeText {
        .title {
          font-size: 2.5rem;
        }
        .subTitle {
          font-size: 18px;
        }
      }
    }
  }
}

@media screen and (min-width: 720px) {
  .home {
    .secContainer {
      .homeCard {
        bottom: -10%;
        grid-template-columns: repeat(4, 1fr);
      }
    }
  }
}

@media screen and (min-width: 1024px) {
  .home {
    .secContainer {
      .homeText {
        .subTitle {
          font-size: 20px;
        }
      }

      .homeCard {
        width: 70%;
        // height: 90vh;
      }
    }
  }
}
